// 
// jQuery 
$(function () {
   // 
   //   .open class
   $('#toggler-burger').on('click', function () {
      $(this).toggleClass('open');
   })

   // 
   let lastPos = 0;
   $(window).on('scroll', function () {
      let currentPos = window.scrollY;
      //   
      if (currentPos > lastPos) {
         $('#navbar-toggle').css('top', '-100px');//navbar
      } else {
         $('#navbar-toggle').css('top', '0');//navbar
      }
      lastPos = currentPos;//
   });


   // goTop 
   // scroll  150x
  //  .goTop  .hide  Class class .hide
   $(window).on('scroll',function () {
      if ($(window).scrollTop() > 150) {
        if ($(".goTop").hasClass("hide")) {
          $(".goTop").toggleClass("hide");
        }
      } else {
        $(".goTop").addClass("hide");
      }
    });

   //   goTopBtn html  body  0  0.6
   $('#goTopBtn').on('click',function (e) {
      e.preventDefault();
      $('html , body').animate( { scrollTop: 0,},'600');
    });
    
});



// 
function getRandom(num) {
   return Math.floor(Math.random() * num);
};


// loading 
function toggleLoading(show) {
   //showtrue  loadingflase 
   document.querySelector(".loading").style.display = show ? 'block' : 'none';
};

