// jQuery 
$(function () {
  //  TDX api  header 
  getAuthorizationHeader();


  // - 
  get_activity();
  get_scenicSpot();
  get_restaurant();


  //  - 
  scenicSpot_getAllData();
  //  - 
  activity_getAllData();
  //  - 
  restaurant_getAllData();


  //  
  if (location.pathname === '/activity.html' || location.pathname === '/The-F2E-_-week1-TaiWalk/activity.html') {
    activity_getParameters();
  }

  if (location.pathname === '/scenicSpot.html' || location.pathname === '/The-F2E-_-week1-TaiWalk/scenicSpot.html') {
    scenicSpot_getParameters();
  }

  if (location.pathname === '/restaurant.html' || location.pathname === '/The-F2E-_-week1-TaiWalk/restaurant.html') {
    restaurant_getParameters();
  }

  // loading 
  toggleLoading(true);
  setTimeout(() => { toggleLoading(false);
  }, 3000);
});


// baseUrl
let baseUrl = `https://tdx.transportdata.tw/api/basic/v2/Tourism`;



// jQuery   TDX api  header 
function getAuthorizationHeader() {
  const parameter = {
    grant_type: "client_credentials",
    client_id: "pi20120413-51900829-226c-41ea",
    client_secret: "8094d91e-08e7-403f-81a2-59c5d0426a91"
  };

  let auth_url = "https://tdx.transportdata.tw/auth/realms/TDXConnect/protocol/openid-connect/token";

  $.ajax({
    type: "POST",
    url: auth_url,
    crossDomain: true,
    dataType: 'JSON',
    data: parameter,
    async: false,
    success: function (data) {
      const access_token = JSON.stringify(data.access_token);
      const expires_in = JSON.stringify(data.expires_in);

      //  token 
      const token = document.cookie = `tourToken=${access_token}; expires=${new Date(expires_in)}; path=/`;
    },
    error: function (xhr, textStatus, thrownError) {
      console.log('errorStatus:', textStatus);
      console.log('Error:', thrownError);
    }
  });
}


